<template>
    <div class="grid">
        <span class="cell" v-for="color in pattern" :style="{backgroundColor:colors[color]}"></span>
    </div>
</template>

<script>
    let colors ={
        0:"grey",
        1:"red",
        2:"green",
        3:"orange",
        4:"#0080ff",
        
    }

    
    export default {
        props:['pattern'],
        data(){
            return{
                colors:colors
            }
        }
    }
    
</script>

<style lang="scss">
$dimension:60px;
$small-dimension:50px;

    .grid{
        margin-top:1.5em;
    }

    .cell{
        display:inline-block;
        width:$small-dimension;
        height:$small-dimension;
        border: solid #dcdcdc;
        border-top-width: 4px;
        border-bottom-width: 4px;
        border-left-width: 2px;
        border-right-width: 2px;
        box-sizing:border-box;
        
        &:first-of-type{
            border-left-width: 4px;
        }
        
        &:last-of-type{
            border-right-width: 4px;
        }
    }
    
    @media screen and (min-width: 400px) {
        .cell {
            width:$dimension;
            height:$dimension;
        }
    }
</style>


