<template>
  <div id="app">
     <h2>Two-sided PLL Recognitoin Tester</h2>
   
    <span><strong>Test recognition of: </strong></span>
    <br>
    <span class="option"><label for=""><input type="checkbox" value="three_bar" v-model="pll_list" :checked="true"/>3 Bar</label></span>
    <span class="option"><label for=""><input type="checkbox" value="two_lights" v-model="pll_list" checked/>Double Lights</label></span>
    <span class="option"><label for=""><input type="checkbox" value="lights_two_bar" v-model="pll_list" checked/>Lights & 2 Bar</label></span>
    <span class="option"><label for=""><input type="checkbox" value="lone_lights" v-model="pll_list"/>Lone Lights</label></span>
    <span class="option"><label for=""><input type="checkbox" value="double_two_bar" v-model="pll_list"/>Double 2 Bar</label></span>
    <span class="option"><label for=""><input type="checkbox" value="outside_two_bar" v-model="pll_list"/>Outside 2 Bar</label></span>
    <span class="option"><label for=""><input type="checkbox" value="inside_two_bar" v-model="pll_list"/>Inside 2 Bar</label></span>
    <span class="option"><label for=""><input type="checkbox" value="bookends" v-model="pll_list"/>Bookends No Bar</label></span>
    <span class="option"><label for=""><input type="checkbox" value="no_bookends" v-model="pll_list"/>No Bookends</label></span>
    <span class="option"><label for=""><input type="checkbox" v-model="all" @click="selectAll"/>Select All</label></span>
    
    <div v-if="pll_list.length > 0 && currentPLL >= 0">
      <pllgrid :pattern="pattern"></pllgrid>
      <pllbuttons :perm="pllGroup[currentPLL].perm" @correct="changePLL"></pllbuttons>
    </div>
    <div v-else >
      <h2>Please select at least one two sided PLL recognition group</h2>
    </div>
    
    <br>
    <p>Based on <a href="http://cubing.pt/wp-content/uploads/2017/03/pll2side-20140531.pdf">this guide</a> for two-sided PLL recognition</p>

    
    </div>
</template>

<script>
import * as plls from './assets/data';
import pllgrid from "./components/PllGrid.vue";
import pllbuttons from "./components/PllButtons.vue";
let allPLLS = ['three_bar','two_lights','lights_two_bar','lone_lights','double_two_bar','outside_two_bar','inside_two_bar','bookends','no_bookends']
  
  
export default {
  name: 'app',
  data () {
    return {
      pllcount:null,
      currentPLL:0,
      pll_list:[],
      pllGroup:null,
      all:false
    }
  },
  components:{
    pllgrid,
    pllbuttons
  },
  methods:{
    selectAll(){
      console.log("all: " , this.all);
      if (this.all) {
       this.pll_list = [];
      }
      else{
         this.pll_list = allPLLS;
        
      }
      
    },
    changePLL(){
      console.log("Change PLL")
      let c = this.currentPLL;
      let n = null; // next pll
      
     if (this.pllcount == 0) {
       this.currentPLL = -1;
       return;
       
     }
     
      do{
        n = Math.floor(Math.random() *  this.pllcount);
        
      }while(n == c)
      
      console.log("new: ", n );
      this.currentPLL = n;
      
      console.log("Change PLL finised")
    }
  },
  computed:{
    pattern(){
      console.log('Update Pattern: ',this.currentPLL);
      
      if (this.pll_list.length == 0) {
        return [];
      }
      
    console.log("TEST 1")
      let rotate = Math.floor(Math.random() * 3) + 1;
      let newColor = 0;
      
      if(Math.floor(Math.random() * 10) > 4){
         console.log("TEST 2")
        this.pllGroup[this.currentPLL].pattern.reverse();
        console.log("TEST 3")
      }
      
      
      console.log('Update Pattern foshed: ',this.currentPLL);
     return this.pllGroup[this.currentPLL].pattern.map(x => {
        
        if(x == 0)
        {
          return x;
        }
        
        newColor = x+ rotate;
        
        
        return newColor > 4 ? newColor % 4 : newColor;
      })
      
      
    },
    
  },
  watch:{
      
      pll_list(){
        console.log("pll_list")
        let group = [];
        let length = this.pll_list.length;
        
        for (let i = 0; i < length; i++) {
         group = group.concat(plls[this.pll_list[i]]);
        }
        this.pllcount = group.length
        this.changePLL();
         console.log("pll_list finished")
        this.pllGroup = group;
      }
      
    },
  created(){
    this.pll_list = ['three_bar']
  }
}
</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  width:100%;
  padding:1em;
  margin:0 auto;
  box-sizing: border-box;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.option{
  display:inline-block;
}

 @media screen and (min-width: 800px){
  #app{
    width:800px;
    padding:2em;
  }
}
</style>



