<template>
    <div>
        <div class="light" :class="{wrong:wrong,correct:correct}"></div>
        <button class="btn" v-for="option in options" @click="checkAnswer(option)">{{ option }}</button>
        <br><br>
    </div>
</template>

<script>
    

    
    export default {
        props:['perm'],
        data(){
            return{
               perms:["U","J","F","Z","H","T","R","A","Ga/c","Gb/d","Y","V","N","E"],
               wrong:false,
               correct:false
            }
        },
        computed:{
            options(){
                let o =[this.perm];
                let pid = this.perms.findIndex(x => x == this.perm);
                
                let used =[pid];
                var p,beingUsed;
                
                for(let i = 0; i<3;i++){
                    
                    do {
                        p = Math.floor(Math.random() * this.perms.length);
                        beingUsed = used.findIndex(x => x == p);
                    }
                    while (beingUsed >= 0);

                    used.push(p);
                    o.push(this.perms[p]);
                }
                
                return this.shuffleArray(o);
            }
        },
        methods:{
            shuffleArray(array) {
                for (var i = array.length - 1; i > 0; i--) {
                    var j = Math.floor(Math.random() * (i + 1));
                    [array[i], array[j]] = [array[j], array[i]];
                }
                return array;
            },
            checkAnswer(perm){
                if(perm == this.perm){
                    console.log("Correct");
                    this.wrong = false;
                    this.correct = true;
                    setTimeout(()=>{
                        this.$emit("correct");
                        this.correct = false;
                    },400)
                    
                }
                else{
                    this.wrong = true;
                }
            }
        }
    }
    
</script>

<style lang="scss">
   .btn {
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0px;
  color: #ffffff;
  font-size: 20px;
  background: #3498db;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
  border:2px solid white;
}

.btn:hover {
  background: #3cb0fd;
  text-decoration: none;
}

.light{
     height:10px;
     width:300px;
     margin: 1em auto;
      
     &.wrong{
        background-color:red;
        -webkit-box-shadow: 0px 0px 10px 5px rgba(255,0,0,0.81);
        -moz-box-shadow: 0px 0px 10px 5px rgba(255,0,0,0.81);
        box-shadow: 0px 0px 10px 5px rgba(255,0,0,0.81);
    }
    
    &.correct{
        background-color:rgba(0,255,102,1);
        -webkit-box-shadow: 0px 0px 10px 5px rgba(0,255,102,1);
        -moz-box-shadow: 0px 0px 10px 5px rgba(0,255,102,1);
        box-shadow: 0px 0px 10px 5px rgba(0,255,102,1);
    }
}

</style>


